    addImage(listName) {
        var defaultImage = 'http://masters-lps.com/api/storage/storage/1606411516123_basic.jpg';
        if (listName !== 'imageList') defaultImage = 'http://masters-lps.com/api/storage/storage/1606345081409_footer.png'
        this.setState({
            [listName]: [...this.state[listName], {
                marginTop: 0,
                image_url: defaultImage,
                croppedImageUrl: defaultImage,
                crop: {
                    unit: '%', // default, can be 'px' or '%'
                    width: 100,
                    height: 100
                },
            }]
        })
    }

    removeImage(listName, index) {
        var array = [...this.state[listName]]; // make a separate copy of the array
        array.splice(index, 1);
        this.setState({ [listName]: array });
    }

    onImageListChanged(listName, index, value, column) {
        const clonedArray = [...this.state[listName]];
        clonedArray.splice(index, 1, {
            ...this.state[listName][index],
            [column]: value
        })
        this.setState({ [listName]: clonedArray })
    }

    async onImageCropComplete(listName, index, crop) {

        var imageRef = this[listName][index];

        if (imageRef && crop.width && crop.height) {
            const croppedImageUrl = await this.getCroppedImg(
                listName,
                index,
                imageRef,
                crop,
                'newFile.png'
            );
            this.onImageListChanged(listName, index, croppedImageUrl, 'croppedImageUrl');
        }
    }

    readFile(listName, index, event) {
        const reader = new FileReader();
        reader.addEventListener('load', () => this.onImageListChanged(listName, index, reader.result, 'src'));
        reader.readAsDataURL(event.target.files[0]);
    }

    readActionImageFile(event, column) {
        const reader = new FileReader();
        this[column] = event.target.files[0]
        reader.addEventListener('load', () => this.setState({ [column]: reader.result }));
        reader.readAsDataURL(event.target.files[0]);
    }

    initWhatsapp() {
        var $ = window.$;
        console.error($('#WAButton'))
        $('#WAButton').html('')
        $('#WAButton').floatingWhatsApp({
            phone: this.state.phone_number, //WhatsApp Business phone number International format-
            //Get it with Toky at https://toky.co/en/features/whatsapp.
            headerTitle: this.state.whatsapp_title, //Popup Title
            popupMessage: this.state.whatsapp_popup_message, //Popup Message
            showPopup: true, //Enables popup display
            buttonImage: '<img src="https://rawcdn.githack.com/rafaelbotazini/floating-whatsapp/3d18b26d5c7d430a1ab0b664f8ca6b69014aed68/whatsapp.svg" />', //Button Image
            //headerColor: 'crimson', //Custom header color
            //backgroundColor: 'crimson', //Custom background button color
            position: this.state.whatsapp_position
        });
    }

